import crypto from "crypto"
import { query } from "@/lib/db"
import type { NotificationItem } from "@/lib/types"
import { getOrCreateAdminProfile } from "@/lib/profile"

const NOTIFICATION_TABLE = "admin_notifications"

type NotificationRow = {
  id: string
  profile_id: string
  title: string
  message: string
  type: NotificationItem["type"]
  is_read: 0 | 1
  created_at: Date | string
}

async function ensureNotificationsTable() {
  await query(`
    CREATE TABLE IF NOT EXISTS ${NOTIFICATION_TABLE} (
      id CHAR(36) PRIMARY KEY,
      profile_id CHAR(36) NOT NULL,
      title VARCHAR(191) NOT NULL,
      message TEXT NOT NULL,
      type ENUM('info','success','warning','error') NOT NULL DEFAULT 'info',
      is_read TINYINT(1) DEFAULT 0,
      created_at TIMESTAMP DEFAULT CURRENT_TIMESTAMP,
      updated_at TIMESTAMP DEFAULT CURRENT_TIMESTAMP ON UPDATE CURRENT_TIMESTAMP,
      CONSTRAINT fk_notifications_profile
        FOREIGN KEY (profile_id) REFERENCES admin_profiles(id)
        ON DELETE CASCADE
    )
  `)
}

function mapNotification(row: NotificationRow): NotificationItem {
  return {
    id: row.id,
    profile_id: row.profile_id,
    title: row.title,
    message: row.message,
    type: row.type,
    is_read: Boolean(row.is_read),
    created_at: new Date(row.created_at),
  }
}

export async function createNotification(fields: Omit<NotificationItem, "id" | "created_at">) {
  await ensureNotificationsTable()
  const id = crypto.randomUUID()
  await query(
    `INSERT INTO ${NOTIFICATION_TABLE} (id, profile_id, title, message, type, is_read)
     VALUES (?, ?, ?, ?, ?, ?)`,
    [id, fields.profile_id, fields.title, fields.message, fields.type, fields.is_read ? 1 : 0],
  )
  const [row] = await query<NotificationRow[]>(`SELECT * FROM ${NOTIFICATION_TABLE} WHERE id = ?`, [id])
  return mapNotification(row)
}

export async function getNotifications() {
  await ensureNotificationsTable()
  const profile = await getOrCreateAdminProfile()
  const rows = await query<NotificationRow[]>(
    `SELECT * FROM ${NOTIFICATION_TABLE} WHERE profile_id = ? ORDER BY created_at DESC`,
    [profile.id],
  )
  return rows.map(mapNotification)
}

export async function markNotificationRead(id: string, read: boolean) {
  await ensureNotificationsTable()
  await query(`UPDATE ${NOTIFICATION_TABLE} SET is_read = ?, updated_at = NOW() WHERE id = ?`, [read ? 1 : 0, id])
}

export async function deleteNotification(id: string) {
  await ensureNotificationsTable()
  await query(`DELETE FROM ${NOTIFICATION_TABLE} WHERE id = ?`, [id])
}

export async function deleteAllNotifications(profileId?: string) {
  await ensureNotificationsTable()
  const profile = profileId ? { id: profileId } : await getOrCreateAdminProfile()
  await query(`DELETE FROM ${NOTIFICATION_TABLE} WHERE profile_id = ?`, [profile.id])
}

export async function markAllNotificationsRead(profileId?: string) {
  await ensureNotificationsTable()
  const profile = profileId ? { id: profileId } : await getOrCreateAdminProfile()
  await query(`UPDATE ${NOTIFICATION_TABLE} SET is_read = 1 WHERE profile_id = ?`, [profile.id])
}
